
.example-warper {
    width: 100%;
    padding: 20px 5%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    /* border: 1px #cfcece dashed; */
}

.button {
    font-family: "Roboto", Arial, sans-serif;
    color: #000000;
    cursor: pointer;
    padding: 0px 30px;
    display: inline-block;
    margin: 10px 15px;
    text-transform: uppercase;
    line-height: 4em;
    letter-spacing: 1.5px;
    font-size: 1em;
    outline: none;
    position: relative;
    font-size: 14px;
    /* border: 3px solid #cfcece; */
    background-color: #ffffff;
    border-radius: 15px 15px 15px 15px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.button:hover,
.button.hover {
    border-color: #2980b9;
}
/*
  card 
  */
.card {
    font-size: 12px;
}
.card > .header {
    width: 100%;
    font-size: 14px;
    text-align: center;
}

.modal {
    font-size: 20px;
}
.modal > .header {
    width: 100%;
    color: #feeecc;
    font-size: 60px;
    text-align: center;
    padding: 50px;
}
.modal > .pcontent {
    position: relative;
    width: 50%;
    padding: 10px 5px;
    margin: auto;
    background-color: aliceblue;
    
}
.modal > .actions {
    margin: auto;
}
.modal > .actions {
    width: 100%;
    padding: 10px 5px;
    text-align: center;
}
.modal > .close {
    cursor: pointer;
    position: relative;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: 100px;
    top: 100px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
}

.example-warper-start {
    width: 100%;
    padding: 20px 5%;
    display: flex;
    flex-wrap: wrap;
    /* border: 1px #cfcece dashed; */
}

.menu {
    width: 200px;
    display: flex;
    flex-direction: column;
    background: #ffffff;
}
.menu-item {
    cursor: pointer;
    padding: 5px;
    height: 28px;
    border-top: 1px solid rgb(187, 187, 187);
}
.menu-item:hover {
    color: #2980b9;
}
