    font-weight: 400;
    line-height: 1.2;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: block;
}

div.inner02 {
    height: 18px;
    color: rgb(0, 102, 33);
    font-size: 14px;
    line-height: 16px;
    white-space: nowrap;
    overflow: hidden;
