nav.a1 {
    width: 1300px;
    margin: 0 auto;
}

ul.pagination {
    display: inline-block;
    padding: 0;
    margin: 0;
}

ul.pagination li {
    display: inline;
}

ul.pagination li a {
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
}

ul.pagination li a.active {
    background-color: #4caf50;
    color: white;
}

ul.pagination li a:hover:not(.active) {
    background-color: #ddd;
}

ul.pagination li a {
    border-radius: 5px;
}

ul.pagination li a.active {
    border-radius: 5px;
}

ul.pagination li a {
    transition: background-color 0.3s;
}

.content {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

div.inner {
    font-family: arial, sans-serif;
    width: 600px;
}

body,
html,
#root,
.app,
.container {
    width: 100%;
    height: 100%;
}

.container {
    font-size: 28px;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #282c34;
}

.loginForm {
    box-sizing: border-box;
    width: 100%;
    max-width: 600px;
    border-radius: 5px;
    background: #00a6d6;
    padding: 10px;
    position: absolute;
    top: 30%;
    left: 34%;
}

.inputField {
    padding-top: 16px;
}

.input {
    box-sizing: border-box;
    outline: none;
    border: solid 2px #1189de;
    border-radius: 4px;
    color: #292929;
    width: 100%;
    padding: 12px;
    font-size: 14px;
    background: rgba(255, 255, 255, 1);
}

.submitButton {
    padding-top: 16px;
}

.btnMe {
    width: 100%;
    min-width: 280px;
    color: #1189de;
    padding: 12px;
    font-size: 14px;
    font-weight: bold;
    border: solid 2px #1189de;
    border-radius: 4px;
    background: #fff;
    cursor: pointer;
}

.loginTitle {
    color: #ffffff;
    font-size: 30px;
    text-align: center;
}

.logui_tr {
    height: 60px;
    background: #36304a;
}

.logui_th {
    font-family: OpenSans-Regular;
    font-size: 18px;
    color: #fff;
    line-height: 1.2;
    font-weight: unset;
}

.logui_td {
    font-family: OpenSans-Regular;
    font-size: 18px;
    color: rgb(29, 29, 29);
    line-height: 1.2;
    font-weight: unset;
    padding-right: 40px;
}

.logui_td_id {
    width: 200px;
    padding-right: 40px;
}

.logui_td_sp {
    font-family: OpenSans-Regular;
    font-size: 15px;
    color: rgb(29, 29, 29);
    line-height: 1.2;
    font-weight: unset;
    padding-right: 40px;
}

.msg {
    color: rgb(255, 255, 255);
    width: 100%;
    text-align: center;
}
